import React from 'react'
import Carousel from 'react-elastic-carousel';

import './UsersComment.scss'
import {FaQuoteLeft} from 'react-icons/fa'
import {Data} from './data'

const breakPoints = [
    { width: 1, itemsToShow: 1 },
    { width: 500, itemsToShow: 2 },
    { width: 800, itemsToShow:3 }
];

const UsersComment = () => {
    return (
        <div className='shared-users-comment'>
            <div className='quotes'>
                <span data-aos='fade-left' data-aos-delay='200'><FaQuoteLeft /></span>
            </div>
            <h1 data-aos='fade-right' data-aos-delay='200'>What our users say</h1>
            <div className='slider-bg' data-aos='fade-left' data-aos-delay='300'></div>
            <Carousel className="slider"  breakPoints={breakPoints}>
                {Data.map((props) => {
                    return(
                        <div className='slider-item'>
                            <div className='profile'
                                style={{content: `url(${props.img})`}}>
                            </div>
                            <div className='line'></div>
                            <p>{props.description}</p>
                            <div className='name'>
                                <h6>{props.name}</h6>
                                <small>{props.title}</small>
                            </div>
                        </div>
                    )
                })}
            </Carousel>
        </div>
    )
}

export default UsersComment
